import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor() {
    this.state = {
      url: '',
      videoUrl: '',
    };
  }


  async start() {
    this.model = new AppModel();
    const data = await this.model.getClipNames();
    this.view = new AppView(data);
    AppView.search();
    const input = document.getElementById('search');
    const that = this;
    document.body.style.setProperty('--body-width', `${document.body.offsetWidth}px`);

    input.addEventListener('input', () => {
      that.model.page = 1;
      that.update(input.value, null);
    });

    window.addEventListener('resize', () => {
      document.body.style.setProperty('--body-width', `${document.body.offsetWidth}px`);
    });

    AppView.init();
    this.view.pageRender(false);
    const pageNext = document.getElementById('page-next');
    const pagePrevious = document.getElementById('page-previous');

    pageNext.addEventListener('click', () => {
      this.model.scrLeft = true;
      that.slider.dispatchEvent(new Event('mouseup'));
    });
    pagePrevious.addEventListener('click', () => {
      this.model.scrLeft = false;
      that.slider.dispatchEvent(new Event('mouseup'));
    });

    this.slider = document.querySelector('.grid-container');

    this.slider.addEventListener('mousedown', (e) => {
      this.model.isDown = true;
      this.slider.classList.add('active');
      this.model.startX = e.pageX - this.slider.offsetLeft;
      this.model.scrollLeft = this.slider.scrollLeft;
    });

    this.slider.addEventListener('mouseleave', () => {
      this.model.isDown = false;
      this.slider.classList.remove('active');
    });

    this.slider.addEventListener('mouseup', () => {
      if (this.model.scrLeft === true) {
        if (this.model.page % 2 === 0) {
          that.update(input.value, this.model.nextToken);
        }

        this.slider.scrollLeft = document.body.offsetWidth * this.model.page;
        if (this.model.page < this.view.inner.length) {
          this.model.page += 1;
        }
      } else {
        if (this.model.page > 1) {
          this.model.page -= 1;
        }
        this.slider.scrollLeft = document.body.offsetWidth * (this.model.page - 1);
      }
      this.model.isDown = false;
      this.slider.classList.remove('active');
      this.view.page = this.model.page;
      this.view.pageRender(true);
    });

    this.slider.addEventListener('mousemove', (e) => {
      if (!this.model.isDown) return;
      e.preventDefault();
      const x = e.pageX - this.slider.offsetLeft;
      const walk = x - this.model.startX;
      if (walk < 0) {
        this.model.scrLeft = true;
      } else if (walk > 0) {
        this.model.scrLeft = false;
      }
    });
  }

  async update(value, token) {
    if (token === null) {
      this.model.url = `https://www.googleapis.com/youtube/v3/search?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&type=video&part=snippet&maxResults=15&q=${value}`;
    } else {
      this.model.url = `https://www.googleapis.com/youtube/v3/search?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&type=video&part=snippet&maxResults=15&q=${value}&pageToken=${this.model.nextToken}`;
    }

    const data = await this.model.getClipNames();

    this.view.titles = data.titles;
    this.view.description = data.description;
    this.view.publishedAt = data.publishedAt;
    this.view.channelTitle = data.channelTitle;
    this.view.preview = data.preview;
    this.view.url = data.url;

    this.model.videoUrl = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&id=${data.url},REu2BcnlD34,qbPTdW7KgOg&part=snippet,statistics`;
    const dataViewCount = await this.model.getViewCount();
    this.view.viewCount = dataViewCount.viewCount;
    this.view.page = this.model.page;
    this.model.nextToken = data.nextToken;

    this.view.render(token === null);
  }
}
