export default class AppView {
  constructor(data) {
    this.titles = data.titles;
    this.description = data.description;
    this.publishedAt = data.publishedAt;
    this.channelTitle = data.channelTitle;
    this.preview = data.preview;
    this.url = data.url;
    this.viewCount = data.viewCount ? data.viewCount : [];
    this.page = 1;
    this.inner = [];
  }

  static init() {
    let container = document.getElementById('container');
    let gridContainer = document.getElementById('grid-container');

    if (gridContainer === null) {
      container = document.createElement('div');
      container.id = 'container';
      container.className = 'container';
      document.body.appendChild(container);
      gridContainer = document.createElement('div');
      gridContainer.id = 'grid-container';
      gridContainer.className = 'grid-container';
      container.appendChild(gridContainer);
    }
  }

  static search() {
    const formEl = document.createElement('form');
    const link = document.createElement('link');

    link.rel = 'stylesheet';
    link.href = 'https://use.fontawesome.com/releases/v5.8.2/css/all.css';
    link.integrity = 'sha384-oS3vJWv+0UjzBfQzYUhtDYW+Pj2yciDJxpsK1OYPAYjqT085Qq/1cq5FLXAZQ7Ay';
    link.crossorigin = 'anonymous';

    document.head.appendChild(link);
    formEl.innerHTML = "<input id = 'search' type='text' placeholder='search something here'>";
    document.body.appendChild(formEl);
  }

  pageRender(display) {
    let pageContainer = document.getElementById('page-container');
    let page = document.getElementById('page');
    let pageNext = document.getElementById('page-next');
    let pagePrevious = document.getElementById('page-previous');
    if (pageContainer === null) {
      pageContainer = document.createElement('div');
      pageContainer.id = 'page-container';
      pageContainer.className = 'page-container';
      document.body.appendChild(pageContainer);
      page = document.createElement('div');
      page.id = 'page';
      page.className = 'page';

      pageContainer.appendChild(page);
    }
    pageContainer.style.display = display ? '' : 'none';
    page.innerHTML = this.page;

    if (pageNext === null) {
      pageNext = document.createElement('div');
      pageNext.id = 'page-next';
      pageNext.className = 'page-next';
      pageNext.style.display = this.page <= this.inner.length ? '' : 'none';
      pageContainer.insertBefore(pageNext, page.nextSibling);
    }
    pageNext.style.display = this.page <= this.inner.length ? '' : 'none';
    pageNext.innerHTML = this.page + 1;

    if (pagePrevious === null) {
      pagePrevious = document.createElement('div');
      pagePrevious.id = 'page-previous';
      pagePrevious.className = 'page-previous';
      pageContainer.insertBefore(pagePrevious, page);
    }
    pagePrevious.style.display = this.page > 1 ? '' : 'none';
    pagePrevious.innerHTML = this.page - 1;
  }

  render(clean) {
    const gridContainer = document.getElementById('grid-container');
    if (clean) {
      gridContainer.innerHTML = '';
      this.inner = [];
    }

    for (let i = 0; i < this.titles.length; i += 1) {
      this.inner.push(`<img id = 'preview' class = 'preview' src = '${this.preview[i]}'/><p class='channelName'><a href='https://www.youtube.com/watch?v=${this.url[i]}'>${this.titles[i]}</a></p><div class='description'>${this.description[i]}</div><p>${this.publishedAt[i]}</p><p><i class="fas fa-male"></i>${this.channelTitle[i]}</p><p>${this.viewCount[i]}</p>`);
    }

    document.body.style.setProperty('--col-count', this.inner.length);
    gridContainer.innerHTML = this.inner.map(it => `<div id='item' class='item'>${it}</div>`).join('');


    this.pageRender(true);
  }
}
