export default class AppModel {
  constructor() {
    this.url = '';
    this.videoUrl = '';
    this.isDown = false;
    this.startX = 0;
    this.scrollLeft = 0;
    this.scrLeft = false;
    this.page = 1;
    this.nextToken = null;
    this.previousToken = null;
  }

  static extractViewCount(data) {
    const items = {};
    items.viewCount = data.items.map(clip => clip.statistics.viewCount);

    return items;
  }

  static extractClipNames(data) {
    const clipItem = {};
    clipItem.titles = data.items.map(clip => clip.snippet.title);
    clipItem.description = data.items.map(clip => clip.snippet.description);
    clipItem.publishedAt = data.items.map(clip => clip.snippet.publishedAt);
    clipItem.channelTitle = data.items.map(clip => clip.snippet.channelTitle);
    clipItem.preview = data.items.map(clip => clip.snippet.thumbnails.medium.url);
    clipItem.url = data.items.map(clip => clip.id.videoId);
    clipItem.nextToken = data.nextPageToken;
    this.nextToken = data.nextPageToken;

    return clipItem;
  }

  async getClipNames() {
    let url = '';
    if (this.url === '') {
      url = 'https://www.googleapis.com/youtube/v3/search?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&type=video&part=snippet&maxResults=15&q=scrum';
    } else {
      // eslint-disable-next-line prefer-destructuring
      url = this.url;
    }

    const responce = await fetch(url);
    const data = await responce.json();

    return AppModel.extractClipNames(data);
  }

  async getViewCount() {
    let videoUrl = '';
    if (this.videoUrl === '') {
      videoUrl = 'https://www.googleapis.com/youtube/v3/videos?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&id=nq4aU9gmZQk,REu2BcnlD34,qbPTdW7KgOg&part=snippet,statistics';
    } else {
      // eslint-disable-next-line prefer-destructuring
      videoUrl = this.videoUrl;
    }


    const responce = await fetch(videoUrl);
    const data = await responce.json();

    return AppModel.extractViewCount(data);
  }
}
